import React from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Container, Box, Paper, ThemeProvider } from '@mui/material';
import Home from './pages/Home';
import Events from './pages/Events';
import Members from './pages/Members';
import EventDetail from './pages/EventDetail';
import MemberDetail from './pages/MemberDetail';
import theme from './theme';

function App() {
  const lastUpdateDate = new Date().toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column', 
          minHeight: '100vh' 
        }}>
          <AppBar position="static" color="primary">
            <Toolbar>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                ISPSG
              </Typography>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Link to="/" style={{ color: 'white', textDecoration: 'none' }}>Home</Link>
                <Link to="/events" style={{ color: 'white', textDecoration: 'none' }}>Events</Link>
                <Link to="/members" style={{ color: 'white', textDecoration: 'none' }}>Members</Link>
              </Box>
            </Toolbar>
          </AppBar>
          <Container maxWidth="lg" sx={{ mt: 4, flex: 1 }}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/events" element={<Events />} />
              <Route path="/events/:eventId" element={<EventDetail />} />
              <Route path="/members" element={<Members />} />
              <Route path="/members/:memberId" element={<MemberDetail />} />
            </Routes>
          </Container>
          <Paper 
            component="footer" 
            sx={{ 
              py: 2, 
              mt: 'auto', 
              backgroundColor: (theme) => theme.palette.grey[200]
            }}
          >
            <Container maxWidth="lg">
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Typography variant="body2" color="text.secondary">
                  © 2025 ISPSG. All rights reserved.
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Last updated: {lastUpdateDate}
                </Typography>
              </Box>
            </Container>
          </Paper>
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App;
